import React, { useEffect, useMemo, useRef, useState } from 'react'
import CustomTable from '../../reuseableComponents/customTable/CustomTable';
import { activeTabPermission, getBadgeColor, isPermission } from '../../../utils/common';
import { useNavigate } from 'react-router-dom';
import { Icon } from '../../commanComponent';
import { useDispatch } from 'react-redux';
import { employeeActions } from '../../../core/resource/actions';
import { CustomButton } from '../../globalComponents';
import BulkUploadEmployee from './BulkUploadEmployee';
import { payrollActions } from '../../../core/resource/actions';
import { Link } from 'react-router-dom';
import { CustomTabs } from '../../reuseableComponents';

const EmployeeTable = (props) => {
    const {
        activeTabEmployeesData,
        isLoading,
        currentPage,
        setCurrentPage,
        setActiveTabType,
        handleViewEmployeeDetail,
        toggleEmpOnboardingDetail,
        handleCreateUpdateEmployeeModal,
        allEmployeeListData,
        bulkUpdateEmployeeData,
    } = props
   
    const [isShowModal, setIsShowModal] = useState(false)
    const fileInputRef = useRef(null);
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [permissions,setPermissions]=useState('')
    const [hasPermission,setHasPermission]=useState(false)
    const tableTabsRequest = [
        { name: "All Employees", onClickFun: (id)  => handleActiveTab(id), id: "allEmployees", permissionId :"all_employees"},
        { name: "Active Employee", onClickFun: (id) => handleActiveTab(id), id: "active",permissionId :"active_employees" },
        { name: "Inactive Employee", onClickFun: (id) => handleActiveTab(id), id: "inactive",permissionId :"inactive_employees" },

    ];
    const activeEmployeeTab =activeTabPermission(tableTabsRequest)

    useEffect(()=>{
        checkPermissions()
    },[])
   
    const checkPermissions = () => {
        let permissionArr = [
          "update_employee",
          "view_employee_detail",
          "employee_history_action",
          "view_onboarding_details",
          "send_offer_letter"
        ];
        let permissionData ={}
        permissionArr.forEach((item) => {
          let per = isPermission(item); 
          permissionData ={...permissionData,[item]:per}
         
        });
        setPermissions(permissionData);
        const showPer = Object.values(permissionData).some(item => item === true);
        setHasPermission(showPer)  
    };
      

    const handleActiveTab = (type) => {
   
        setActiveTabType(type === "allEmployees" ? "" : type)
        setCurrentPage(1)
    }

    const toggleEmpHistory = (item) => {
        dispatch(employeeActions.selectedEmployeeData(item))
        navigate("employee-history")
    }

    const setSelectedEmpData = (data) => {
            dispatch(payrollActions.selectedEmployeeData(data))
        }

    const allEmployeeField = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp Id' },
        { key: 'name', label: 'Full Name' },
        { key: 'mobile', label: 'Mobile' },
        { key: 'email', label: 'Email' },
        { key: 'account_status', label: 'Account Status' },
       (hasPermission && { key: 'action', label: 'Action' })
    ];

    const scopedSlotsAllEmployee = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        account_status: item => (
            <span
                className={`badge ${getBadgeColor(item.account_status)}`}
            >
                {item.account_status}
            </span>
        ),


        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    {permissions?.view_employee_detail &&<li>
                        <span className="dropdown-item"
                            onClick={() => handleViewEmployeeDetail(item)}
                        >
                            View Detail</span>
                    </li>}
                    {permissions?.update_employee &&<li>
                        <span className="dropdown-item"
                            onClick={() => handleCreateUpdateEmployeeModal(item)}
                        >Update</span>
                    </li>}

                   {permissions?.employee_history_action &&<li>
                        <span
                            onClick={() => toggleEmpHistory(item)}
                            className="dropdown-item"
                        >Employee History</span>
                    </li>}
                  {permissions?.view_onboarding_details &&  <li>
                        <span className="dropdown-item"
                            onClick={() => toggleEmpOnboardingDetail(item)}
                        >
                            View Onboarding Detail</span>
                    </li>}
                    
                    {permissions?.send_offer_letter &&<li>
                        <Link className="dropdown-item" to={`add-employee-salary`}
                            onClick={() => setSelectedEmpData(item)}
                        >
                            Send Offer Letter</Link>
                    </li>}



                </ul>
            </div>
        )
    }), [activeTabEmployeesData,permissions]);


    const handleButtonClick = () => {
        // Open file selector
        fileInputRef.current.click();
    };

    // const handleFileUpload = (event) => {
    //     const file = event.target.files[0];
    //     if (file) {
    //         const payload = {
    //             file: file
    //         }
    //         bulkUpdateEmployeeData(payload)
    //     }
    // };

    const toggleBulkUpload = () => {
        setIsShowModal(!isShowModal)
    }


    return (
        <>
            <div className="cardBox myRequest mb-3">
                <div className='d-flex justify-content-between'>
                    <div className="cardHead">

                        <img src="images/myrequest-icon.svg" alt="" width="35" />
                        <h3 className="ms-2">Employees</h3>
                    </div>
                    <div>
                        <>
                            <CustomButton
                                classNames={"btn btn-primary gradientBg"}
                                handleClick={toggleBulkUpload}
                                text={"Bulk Upload"}
                                icon={<Icon className={"fa fa-upload"} />}
                                slug={"employee_bulk_upload"}
                            />
                           
                        </>
                    </div>
                </div>
                 <CustomTabs tabs={tableTabsRequest} type="employeeTab" />
      
                <div className="tab-content">
                    <div className={`tab-pane fade ${activeEmployeeTab === "allEmployees" ? "show active" : ""}`} id="allEmployees">
                        <div className="table-responsive tableDiv text-nowrap overflowTable">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />

                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeEmployeeTab === "active" ? "show active" : ""}`} id="active">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeEmployeeTab === "inactive" ? "show active" : ""}`} id="inactive">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={allEmployeeListData?.data}
                                fields={allEmployeeField}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={allEmployeeListData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsAllEmployee}
                            />
                        </div>
                    </div>
                    {isShowModal && <BulkUploadEmployee
                        handleOpenClose={toggleBulkUpload}
                        openModel={isShowModal}
                        bulkUpdateEmployeeData={bulkUpdateEmployeeData}
                    />}


                </div>
            </div>
        </>
    )
}

export default EmployeeTable
